html {
    font-size: 16px;
    line-height: 1.3;
}

body {
  margin: 0;
  padding: 0;  
  font-family: 'IBM Plex Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: var(--secondary-color);
  /* background-color: #F7F7F7!important; */
  background-color: white!important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary-color: #70757a;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0;
    padding: 0;
    font-family: 'IBM Plex Mono', monospace;
    font-weight: 600;
    line-height: 1.2;
}
.emoji {
  height: 1rem;
}
h1 {
    letter-spacing: -1px;
    font-size: 2.5rem;
}
h1 .emoji {
    height: 2.5rem;
}
h2 {
    letter-spacing: -1px;
    font-size: 2rem;
}
h2 .emoji {
  height: 2rem;
}
h3 {
    letter-spacing: -0.5px;
    font-size: 1.75rem;
}
h3 .emoji {
    height: 1.75rem;
}
h4 {
    letter-spacing: -0.5px;
    font-size: 1.5rem;
}
h4 .emoji {
    height: 1.5rem;
}
h5 {
    letter-spacing: -0.5px;
    font-size: 1.25rem;
}
h1 .emoji {
    height: 1.25rem;
}

@media screen and (min-width: 992px) {
    h1 {
        letter-spacing: -1px;
        font-size: 4rem;
    }
    h1 .emoji {
        height: 4rem;
    }
    h2 {
        letter-spacing: -1px;
        font-size: 3rem;
    }
    h2 .emoji {
        height: 3rem;
    }
    h3 {
        letter-spacing: -0.5px;
        font-size: 2.5rem;
    }
    h3 .emoji {
        height: 2.5rem;
    }
    h4 {
        letter-spacing: -0.5px;
        font-size: 2rem;
    }
    h4 .emoji {
        height: 2rem;
    }
    h5 {
        letter-spacing: -0.5px;
        font-size: 1.5rem;
    }
    h5 .emoji {
        height: 1.5rem;
    }
}

